import { Button, TextInput } from 'ether-ui';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Integration, WhatsAppIntegrationConfig } from '../../../types/integration';
import env from '../../common/env';
import { Password } from '../../components';
import { useAuthStore } from '../../services/auth';
import { useIntegrationStore } from '../../services/integration';

interface WhatsAppIntegrationFormProps {
    onCancel: () => void;
    integration?: Integration;
}

export const WhatsAppIntegrationForm: React.FC<WhatsAppIntegrationFormProps> = ({ onCancel, integration: integrationProp }) => {
    const navigate = useNavigate();
    const { user } = useAuthStore();
    const { createIntegration, updateIntegration, isLoading } = useIntegrationStore();
    const isEditMode = !!integrationProp;
    const [name, setName] = useState('');
    const [appId, setAppId] = useState('');
    const [appName, setAppName] = useState('');
    const [secret, setSecret] = useState('');
    const [config, setConfig] = useState<WhatsAppIntegrationConfig>({
        fromNumber: '',
    });
    const [copiedWebhook, setCopiedWebhook] = useState(false);
    const [error, setError] = useState('');

    useEffect(() => {
        if (integrationProp) {
            setName(integrationProp.name || '');
            setAppId(integrationProp.appId || '');
            setAppName(integrationProp.appName || '');
            setSecret(integrationProp.secret || '');
            setConfig(integrationProp.config as WhatsAppIntegrationConfig);
        }
    }, [integrationProp]);

    const webhookUrl =
        integrationProp?.id && user?.organizationId
            ? `${env.apiExternalBasePath}/api/webhooks/whatsapp/${user.organizationId}/${integrationProp.id}/callback`
            : '';

    const handleCopyWebhook = async () => {
        if (webhookUrl) {
            await navigator.clipboard.writeText(webhookUrl);
            setCopiedWebhook(true);
            setTimeout(() => setCopiedWebhook(false), 2000);
        }
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');
        try {
            if (isEditMode && integrationProp?.id) {
                await updateIntegration(integrationProp.id, {
                    name,
                    appId,
                    appName,
                    secret,
                    config,
                });
            } else {
                await createIntegration({
                    type: 'whatsapp',
                    name,
                    appId,
                    appName,
                    secret,
                    config,
                });
            }
            navigate('/integrations');
        } catch (err: any) {
            setError(err?.message || `Failed to ${isEditMode ? 'update' : 'create'} integration`);
        }
    };

    return (
        <form onSubmit={handleSubmit} className="bg-white rounded-lg border border-gray-200 p-6 space-y-6">
            <div>
                <h3 className="text-lg font-semibold text-gray-900 mb-4">WhatsApp Integration</h3>
                <p className="text-sm text-gray-600">Configure your WhatsApp Business API connection</p>
            </div>

            {error && (
                <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                    <p className="text-red-600 text-sm">{error}</p>
                </div>
            )}

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Integration Name</label>
                <TextInput value={name} onChange={(e) => setName(e.value)} placeholder="e.g., WhatsApp Business Account" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">WSBA ID</label>
                <TextInput
                    value={appId}
                    onChange={(e) => setAppId(e.value)}
                    placeholder="Enter your WhatsApp Business Account ID"
                    required
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">App Name</label>
                <TextInput value={appName} onChange={(e) => setAppName(e.value)} placeholder="Enter your WhatsApp App Name" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Access Token</label>
                <Password value={secret} onChange={(e) => setSecret(e.value)} placeholder="Enter your access token" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Phone Number ID</label>
                <TextInput
                    value={config.phoneNumberId || ''}
                    onChange={(e) => setConfig({ ...config, phoneNumberId: e.value })}
                    placeholder="Enter your phone number ID"
                    required
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">From Number</label>
                <TextInput
                    value={config.fromNumber}
                    onChange={(e) => setConfig({ ...config, fromNumber: e.value })}
                    placeholder="+1234567890"
                    required
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Callback URL (Optional)</label>
                <TextInput
                    value={config.callbackUrl || ''}
                    onChange={(e) => setConfig({ ...config, callbackUrl: e.value })}
                    placeholder="https://your-server.com/webhook"
                />
                <p className="text-xs text-gray-500 mt-1">URL to handle and respond to user queries</p>
            </div>

            {webhookUrl && (
                <div className="bg-purple-50 border border-purple-200 rounded-lg p-4">
                    <label className="block text-sm font-medium text-gray-700 mb-2">Webhook URL</label>
                    <div className="flex items-center gap-2">
                        <input
                            type="text"
                            readOnly
                            value={webhookUrl}
                            className="flex-1 px-3 py-2 text-sm bg-white border border-gray-300 rounded-md font-mono text-gray-700"
                        />
                        <Button type="button" variant="default" size="sm" onClick={handleCopyWebhook}>
                            {copiedWebhook ? 'Copied!' : 'Copy'}
                        </Button>
                    </div>
                    <p className="text-xs text-gray-600 mt-2">
                        Configure this webhook URL in your WhatsApp Business API settings to receive delivery status updates and user
                        responses.
                    </p>
                </div>
            )}

            <div className="flex justify-end space-x-3 pt-4 border-t">
                <Button type="button" variant="default" onClick={onCancel}>
                    Cancel
                </Button>
                <Button type="submit" disabled={isLoading}>
                    {isLoading ? (isEditMode ? 'Updating...' : 'Creating...') : isEditMode ? 'Update Integration' : 'Create Integration'}
                </Button>
            </div>
        </form>
    );
};
