import { Button, Dropdown, TextInput } from 'ether-ui';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { EmailIntegrationConfig, Integration } from '../../../types/integration';
import { Password } from '../../components';
import { useIntegrationStore } from '../../services/integration';

interface EmailIntegrationFormProps {
    onCancel: () => void;
    integration?: Integration;
}

const providerOptions = [
    { label: 'SMTP', value: 'smtp' },
    { label: 'SendGrid', value: 'sendgrid' },
    { label: 'Mailgun', value: 'mailgun' },
    { label: 'AWS SES', value: 'aws-ses' },
];

export const EmailIntegrationForm: React.FC<EmailIntegrationFormProps> = ({ onCancel, integration }) => {
    const navigate = useNavigate();
    const { createIntegration, updateIntegration, isLoading } = useIntegrationStore();
    const isEditMode = !!integration;
    const [name, setName] = useState('');
    const [appId, setAppId] = useState('');
    const [appName, setAppName] = useState('');
    const [secret, setSecret] = useState('');
    const [config, setConfig] = useState<EmailIntegrationConfig>({
        provider: 'smtp',
        fromEmail: '',
        fromName: '',
    });
    const [error, setError] = useState('');

    useEffect(() => {
        if (integration) {
            setName(integration.name || '');
            setAppId(integration.appId || '');
            setAppName(integration.appName || '');
            setSecret(integration.secret || '');
            setConfig(integration.config as EmailIntegrationConfig);
        }
    }, [integration]);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');
        try {
            if (isEditMode && integration?.id) {
                await updateIntegration(integration.id, {
                    name,
                    appId,
                    appName,
                    secret,
                    config,
                });
            } else {
                await createIntegration({
                    type: 'email',
                    name,
                    appId,
                    appName,
                    secret,
                    config,
                });
            }
            navigate('/integrations');
        } catch (err: any) {
            setError(err?.message || `Failed to ${isEditMode ? 'update' : 'create'} integration`);
        }
    };

    return (
        <form onSubmit={handleSubmit} className="bg-white rounded-lg border border-gray-200 p-6 space-y-6">
            <div>
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Email Integration</h3>
                <p className="text-sm text-gray-600">Configure your email service provider</p>
            </div>

            {error && (
                <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                    <p className="text-red-600 text-sm">{error}</p>
                </div>
            )}

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Integration Name</label>
                <TextInput value={name} onChange={(e) => setName(e.value)} placeholder="e.g., Production Email Server" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">App ID</label>
                <TextInput value={appId} onChange={(e) => setAppId(e.value)} placeholder="Enter your App ID" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">App Name</label>
                <TextInput value={appName} onChange={(e) => setAppName(e.value)} placeholder="Enter your App Name" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">API Secret</label>
                <Password value={secret} onChange={(e) => setSecret(e.value)} placeholder="Enter your API secret" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Provider</label>
                <Dropdown
                    options={providerOptions}
                    value={config.provider}
                    onChange={(value) => setConfig({ ...config, provider: value as any })}
                />
            </div>

            {config.provider === 'smtp' && (
                <>
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">SMTP Host</label>
                        <TextInput
                            value={config.host || ''}
                            onChange={(e) => setConfig({ ...config, host: e.value })}
                            placeholder="smtp.example.com"
                            required
                        />
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">SMTP Port</label>
                        <TextInput
                            type="number"
                            value={config.port?.toString() || ''}
                            onChange={(e) => setConfig({ ...config, port: parseInt(e.value) })}
                            placeholder="587"
                            required
                        />
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">Username</label>
                        <TextInput
                            value={config.username || ''}
                            onChange={(e) => setConfig({ ...config, username: e.value })}
                            placeholder="your-username"
                            required
                        />
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">Password</label>
                        <Password
                            value={config.password || ''}
                            onChange={(e) => setConfig({ ...config, password: e.value })}
                            placeholder="your-password"
                            required
                        />
                    </div>
                </>
            )}

            {(config.provider === 'sendgrid' || config.provider === 'mailgun' || config.provider === 'aws-ses') && (
                <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">API Key</label>
                    <Password
                        value={config.apiKey || ''}
                        onChange={(e) => setConfig({ ...config, apiKey: e.value })}
                        placeholder="Enter your API key"
                        required
                    />
                </div>
            )}

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">From Email</label>
                <TextInput
                    type="email"
                    value={config.fromEmail}
                    onChange={(e) => setConfig({ ...config, fromEmail: e.value })}
                    placeholder="noreply@example.com"
                    required
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">From Name</label>
                <TextInput
                    value={config.fromName}
                    onChange={(e) => setConfig({ ...config, fromName: e.value })}
                    placeholder="Your Company Name"
                    required
                />
            </div>

            <div className="flex justify-end space-x-3 pt-4 border-t">
                <Button type="button" variant="default" onClick={onCancel}>
                    Cancel
                </Button>
                <Button type="submit" disabled={isLoading}>
                    {isLoading ? (isEditMode ? 'Updating...' : 'Creating...') : (isEditMode ? 'Update Integration' : 'Create Integration')}
                </Button>
            </div>
        </form>
    );
};
