import React from 'react';
import { IntegrationType } from '../../../types/integration';

interface IntegrationTypeSelectorProps {
    onSelect: (type: IntegrationType) => void;
}

export const IntegrationTypeSelector: React.FC<IntegrationTypeSelectorProps> = ({ onSelect }) => {
    const integrationTypes = [
        {
            type: 'email' as IntegrationType,
            title: 'Email',
            description: 'Connect SMTP, SendGrid, Mailgun, or AWS SES',
            color: 'blue',
        },
        {
            type: 'gupshup' as IntegrationType,
            title: 'Gupshup',
            description: 'WhatsApp Business API via Gupshup',
            color: 'green',
        },
        {
            type: 'whatsapp' as IntegrationType,
            title: 'WhatsApp',
            description: 'Direct WhatsApp Business API or Twilio',
            color: 'green',
        },
        {
            type: 'sms' as IntegrationType,
            title: 'SMS',
            description: 'Twilio, MessageBird, or AWS SNS',
            color: 'purple',
        },
        {
            type: 'telegram' as IntegrationType,
            title: 'Telegram',
            description: 'Telegram Bot API',
            color: 'cyan',
        },
    ];

    return (
        <div className="bg-white rounded-lg border border-gray-200 p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Select Channel Type</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {integrationTypes.map((item) => (
                    <button
                        key={item.type}
                        onClick={() => onSelect(item.type)}
                        className={`bg-${item.color}-50 border-2 border-${item.color}-200 rounded-lg p-6 text-left hover:bg-${item.color}-100 transition-colors`}
                    >
                        <h4 className="font-semibold text-gray-900 mb-2">{item.title}</h4>
                        <p className="text-sm text-gray-600">{item.description}</p>
                    </button>
                ))}
            </div>
        </div>
    );
};
