import { Button, Dropdown, TextInput } from 'ether-ui';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Integration, SMSIntegrationConfig } from '../../../types/integration';
import { Password } from '../../components';
import { useIntegrationStore } from '../../services/integration';

interface SMSIntegrationFormProps {
    onCancel: () => void;
    integration?: Integration;
}

const providerOptions = [
    { label: 'Twilio', value: 'twilio' },
    { label: 'MessageBird', value: 'messagebird' },
    { label: 'AWS SNS', value: 'aws-sns' },
];

export const SMSIntegrationForm: React.FC<SMSIntegrationFormProps> = ({ onCancel, integration }) => {
    const navigate = useNavigate();
    const { createIntegration, updateIntegration, isLoading } = useIntegrationStore();
    const isEditMode = !!integration;
    const [name, setName] = useState('');
    const [appId, setAppId] = useState('');
    const [appName, setAppName] = useState('');
    const [secret, setSecret] = useState('');
    const [config, setConfig] = useState<SMSIntegrationConfig>({
        provider: 'twilio',
        fromNumber: '',
    });
    const [error, setError] = useState('');

    useEffect(() => {
        if (integration) {
            setName(integration.name || '');
            setAppId(integration.appId || '');
            setAppName(integration.appName || '');
            setSecret(integration.secret || '');
            setConfig(integration.config as SMSIntegrationConfig);
        }
    }, [integration]);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');
        try {
            if (isEditMode && integration?.id) {
                await updateIntegration(integration.id, {
                    name,
                    appId,
                    appName,
                    secret,
                    config,
                });
            } else {
                await createIntegration({
                    type: 'sms',
                    name,
                    appId,
                    appName,
                    secret,
                    config,
                });
            }
            navigate('/integrations');
        } catch (err: any) {
            setError(err?.message || `Failed to ${isEditMode ? 'update' : 'create'} integration`);
        }
    };

    return (
        <form onSubmit={handleSubmit} className="bg-white rounded-lg border border-gray-200 p-6 space-y-6">
            <div>
                <h3 className="text-lg font-semibold text-gray-900 mb-4">SMS Integration</h3>
                <p className="text-sm text-gray-600">Configure your SMS service provider</p>
            </div>

            {error && (
                <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                    <p className="text-red-600 text-sm">{error}</p>
                </div>
            )}

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Integration Name</label>
                <TextInput value={name} onChange={(e) => setName(e.value)} placeholder="e.g., Twilio SMS" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">App ID</label>
                <TextInput value={appId} onChange={(e) => setAppId(e.value)} placeholder="Enter your App ID" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">App Name</label>
                <TextInput value={appName} onChange={(e) => setAppName(e.value)} placeholder="Enter your App Name" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">API Secret</label>
                <Password value={secret} onChange={(e) => setSecret(e.value)} placeholder="Enter your API secret" required />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Provider</label>
                <Dropdown
                    options={providerOptions}
                    value={config.provider}
                    onChange={(value) => setConfig({ ...config, provider: value as any })}
                />
            </div>

            {config.provider === 'twilio' && (
                <>
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">Account SID</label>
                        <TextInput
                            value={config.accountSid || ''}
                            onChange={(e) => setConfig({ ...config, accountSid: e.value })}
                            placeholder="Enter your Twilio Account SID"
                            required
                        />
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">Auth Token</label>
                        <Password
                            value={config.authToken || ''}
                            onChange={(e) => setConfig({ ...config, authToken: e.value })}
                            placeholder="Enter your Twilio Auth Token"
                            required
                        />
                    </div>
                </>
            )}

            {(config.provider === 'messagebird' || config.provider === 'aws-sns') && (
                <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">API Key</label>
                    <Password
                        value={config.apiKey || ''}
                        onChange={(e) => setConfig({ ...config, apiKey: e.value })}
                        placeholder="Enter your API key"
                        required
                    />
                </div>
            )}

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">From Number</label>
                <TextInput
                    value={config.fromNumber}
                    onChange={(e) => setConfig({ ...config, fromNumber: e.value })}
                    placeholder="+1234567890"
                    required
                />
            </div>

            <div className="flex justify-end space-x-3 pt-4 border-t">
                <Button type="button" variant="default" onClick={onCancel}>
                    Cancel
                </Button>
                <Button type="submit" disabled={isLoading}>
                    {isLoading ? (isEditMode ? 'Updating...' : 'Creating...') : (isEditMode ? 'Update Integration' : 'Create Integration')}
                </Button>
            </div>
        </form>
    );
};
