import { Button, TextInput } from 'ether-ui';
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Integration } from '../../../types/integration';
import env from '../../common/env';
import { Password } from '../../components';
import { useAuthStore } from '../../services/auth';
import { useIntegrationStore } from '../../services/integration';

interface GupshupIntegrationFormProps {
    onCancel: () => void;
    integration?: Integration;
}

export const GupshupIntegrationForm: React.FC<GupshupIntegrationFormProps> = ({ onCancel, integration: integrationProp }) => {
    const navigate = useNavigate();
    const { user } = useAuthStore();
    const { createIntegration, updateIntegration, isLoading } = useIntegrationStore();
    const isEditMode = !!integrationProp;
    const [integration, setIntegration] = useState<Partial<Integration>>({ type: 'gupshup' });
    const [copiedWebhook, setCopiedWebhook] = useState(false);
    const [error, setError] = useState('');

    useEffect(() => {
        if (integrationProp) {
            setIntegration(integrationProp);
        }
    }, [integrationProp]);

    const webhookUrl =
        integration.id && user?.organizationId
            ? `${env.apiExternalBasePath}/api/webhooks/gupshup/${user.organizationId}/${integration.id}/callback`
            : '';

    const handleCopyWebhook = async () => {
        if (webhookUrl) {
            await navigator.clipboard.writeText(webhookUrl);
            setCopiedWebhook(true);
            setTimeout(() => setCopiedWebhook(false), 2000);
        }
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');
        try {
            if (isEditMode && integration.id) {
                await updateIntegration(integration.id, {
                    name: integration.name,
                    appId: integration.appId,
                    appName: integration.appName,
                    secret: integration.secret,
                    config: integration.config,
                });
            } else {
                await createIntegration(integration as any);
            }
            navigate('/integrations');
        } catch (err: any) {
            setError(err?.message || `Failed to ${isEditMode ? 'update' : 'create'} integration`);
        }
    };

    return (
        <form onSubmit={handleSubmit} className="bg-white rounded-lg border border-gray-200 p-6 space-y-6">
            <div>
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Gupshup Integration</h3>
                <p className="text-sm text-gray-600">Configure your Gupshup WhatsApp Business API connection</p>
            </div>

            {error && (
                <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                    <p className="text-red-600 text-sm">{error}</p>
                </div>
            )}

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Integration Name</label>
                <TextInput
                    value={integration.name}
                    onChange={(e) => setIntegration((i) => ({ ...i, name: e.value }))}
                    placeholder="e.g., My Gupshup Account"
                    required
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">App Id</label>
                <TextInput
                    value={integration.appId}
                    onChange={(e) => setIntegration((i) => ({ ...i, appId: e.value }))}
                    placeholder="e.g., 3h0bg315-4n14-9d57-1y64-h594gys76732"
                    required
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">App Name</label>
                <TextInput
                    value={integration.appName}
                    onChange={(e) => setIntegration((i) => ({ ...i, appName: e.value }))}
                    placeholder="Your Gupshup app name"
                    required
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">API Key / Secret</label>
                <Password
                    value={integration.secret}
                    onChange={(e) => setIntegration((i) => ({ ...i, secret: e.value }))}
                    placeholder="Enter your Gupshup API key"
                    required
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Source Phone Number</label>
                <TextInput
                    value={integration.config?.phoneNumber}
                    onChange={(e) => setIntegration((i) => ({ ...i, config: { ...i.config, phoneNumber: e.value } }))}
                    placeholder="e.g., 919876543210"
                    required
                />
                <p className="text-xs text-gray-500 mt-1">Your Gupshup registered phone number (with country code, without + sign)</p>
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">API Endpoint</label>
                <TextInput
                    value={integration.config?.apiVersion}
                    onChange={(e) => setIntegration((i) => ({ ...i, config: { ...i.config, apiVersion: e.value } }))}
                    placeholder="v2 or v3"
                />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Callback URL (Optional)</label>
                <TextInput
                    value={integration.config?.callbackUrl || ''}
                    onChange={(e) => setIntegration((i) => ({ ...i, config: { ...i.config, callbackUrl: e.value } }))}
                    placeholder="https://your-server.com/webhook"
                />
                <p className="text-xs text-gray-500 mt-1">URL to handle and respond to user queries</p>
            </div>

            {webhookUrl && (
                <div className="bg-purple-50 border border-purple-200 rounded-lg p-4">
                    <label className="block text-sm font-medium text-gray-700 mb-2">Webhook URL</label>
                    <div className="flex items-center gap-2">
                        <input
                            type="text"
                            readOnly
                            value={webhookUrl}
                            className="flex-1 px-3 py-2 text-sm bg-white border border-gray-300 rounded-md font-mono text-gray-700"
                        />
                        <Button type="button" variant="default" size="sm" onClick={handleCopyWebhook}>
                            {copiedWebhook ? 'Copied!' : 'Copy'}
                        </Button>
                    </div>
                    <p className="text-xs text-gray-600 mt-2">
                        Configure this webhook URL in your Gupshup dashboard to receive delivery status updates and user responses.
                    </p>
                </div>
            )}

            <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
                <p className="text-sm text-blue-800">
                    <strong>Note:</strong> You can find your API key in your Gupshup dashboard under Settings → API Keys.
                </p>
            </div>

            <div className="flex justify-end space-x-3 pt-4 border-t">
                <Button type="button" variant="default" onClick={onCancel}>
                    Cancel
                </Button>
                <Button type="submit" disabled={isLoading}>
                    {isLoading ? (isEditMode ? 'Updating...' : 'Creating...') : isEditMode ? 'Update Integration' : 'Create Integration'}
                </Button>
            </div>
        </form>
    );
};
