import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { IntegrationType } from '../../../types/integration';
import { LoadingSpinner } from '../../components/LoadingSpinner';
import { useIntegrationStore } from '../../services/integration';
import { EmailIntegrationForm } from './EmailIntegrationForm';
import { GupshupIntegrationForm } from './GupshupIntegrationForm';
import { IntegrationTypeSelector } from './IntegrationTypeSelector';
import { SMSIntegrationForm } from './SMSIntegrationForm';
import { WhatsAppIntegrationForm } from './WhatsAppIntegrationForm';

export const IntegrationForm: React.FC = () => {
    const navigate = useNavigate();
    const { id } = useParams<{ id: string }>();
    const isEditMode = !!id;

    const { getIntegrationById, selectedIntegration, isLoading } = useIntegrationStore();
    const [selectedType, setSelectedType] = useState<IntegrationType | null>(null);

    useEffect(() => {
        if (isEditMode && id) {
            getIntegrationById(id);
        }
    }, [id]);

    useEffect(() => {
        if (isEditMode && selectedIntegration) {
            setSelectedType(selectedIntegration.type);
        }
    }, [selectedIntegration, isEditMode]);

    if (isEditMode && !selectedIntegration && isLoading) {
        return (
            <div className="flex items-center justify-center py-12">
                <LoadingSpinner />
            </div>
        );
    }

    if (isEditMode && !selectedIntegration) {
        return (
            <div className="text-center py-12">
                <p className="text-gray-500">Integration not found</p>
            </div>
        );
    }

    const renderForm = () => {
        switch (selectedType) {
            case 'email':
                return (
                    <EmailIntegrationForm
                        onCancel={() => navigate('/integrations')}
                        integration={isEditMode ? selectedIntegration : undefined}
                    />
                );
            case 'gupshup':
                return (
                    <GupshupIntegrationForm
                        onCancel={() => navigate('/integrations')}
                        integration={isEditMode ? selectedIntegration : undefined}
                    />
                );
            case 'whatsapp':
                return (
                    <WhatsAppIntegrationForm
                        onCancel={() => navigate('/integrations')}
                        integration={isEditMode ? selectedIntegration : undefined}
                    />
                );
            case 'sms':
                return (
                    <SMSIntegrationForm
                        onCancel={() => navigate('/integrations')}
                        integration={isEditMode ? selectedIntegration : undefined}
                    />
                );
            default:
                return null;
        }
    };

    return (
        <div className="max-w-4xl mx-auto space-y-6">
            <div>
                <h2 className="text-2xl font-bold text-gray-900">{isEditMode ? 'Edit Integration' : 'Add Integration'}</h2>
                <p className="text-gray-600 mt-1">
                    {isEditMode ? selectedIntegration?.name : 'Connect a new marketing channel to OmniBlast'}
                </p>
            </div>

            {!selectedType && !isEditMode ? (
                <IntegrationTypeSelector onSelect={setSelectedType} />
            ) : (
                <div className="space-y-6">
                    {!isEditMode && (
                        <button onClick={() => setSelectedType(null)} className="text-sm text-blue-600 hover:text-blue-700">
                            ← Back to channel selection
                        </button>
                    )}
                    {renderForm()}
                </div>
            )}
        </div>
    );
};

export default IntegrationForm;
